html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #989898;
  background: linear-gradient(97deg, #989898 19%, #2d366b 80%);
  font-family: "Open Sans", sans-serif; }

.form {
  display: flex;
  grid-column-start: 2;
  grid-row-start: 4; }
  .form__text {
    background-color: #7679a1;
    border-radius: 10px;
    border: 1px solid black;
    display: flex;
    margin: 10px 10px;
    justify-content: end;
    outline: none; }
  @media (max-width: 1000px) {
    .form {
      max-width: 800px; } }
  @media (max-width: 750px) {
    .form {
      max-width: 550px; } }
  @media (max-width: 580px) {
    .form {
      max-width: 320px; } }

.footer {
  align-content: center;
  color: black;
  display: flex;
  grid-column-start: 2;
  grid-row-start: 6;
  justify-content: center; }

.header {
  grid-column-start: 2;
  grid-row-start: 3;
  font-size: 44px;
  margin-left: 30px; }
  .header__space {
    margin-left: 50px; }

.icon {
  border-radius: 7px;
  border: solid 6px currentColor;
  color: #000;
  display: grid;
  grid-column-start: 3;
  grid-row-start: 1;
  height: 22px;
  margin-left: 40px;
  margin-top: 70px;
  transform: rotate(-45deg);
  width: 150px; }
  @media (max-width: 580px) {
    .icon {
      display: none; } }

.icon:before {
  content: "";
  border-left: solid 40px transparent;
  border-right: solid 40px currentColor;
  border-top: solid 10px transparent;
  border-bottom: solid 10px transparent;
  height: 0px;
  left: -90px;
  position: relative;
  top: -5px;
  width: 0px; }
  @media (max-width: 580px) {
    .icon:before {
      display: none; } }

.load {
  background: #989898;
  background: linear-gradient(273deg, #989898 19%, #2d366b 80%);
  border: 2px solid black;
  border-radius: 5px;
  grid-column-start: 2;
  grid-row-start: 5;
  height: 38px;
  margin: 2% 25% 2% 60%; }
  .load:hover {
    background: #989898;
    background: linear-gradient(97deg, #989898 19%, #2d366b 80%); }
  .load:active {
    background: #989898;
    background: linear-gradient(97deg, #989898 19%, #2d366b 80%);
    box-shadow: 0 0 12px 0 #666;
    transform: translateY(2px); }

.main {
  display: grid;
  grid-template-columns: 1fr 6fr 2fr; }

.save {
  background: #989898;
  background: linear-gradient(97deg, #989898 19%, #2d366b 80%);
  border: 2px solid;
  border-radius: 5px;
  grid-column-start: 2;
  grid-row-start: 5;
  height: 38px;
  margin: 2% 5% 2% 80%; }
  .save:hover {
    background: #989898;
    background: linear-gradient(273deg, #989898 19%, #2d366b 80%); }
  .save:active {
    background: #989898;
    background: linear-gradient(273deg, #989898 19%, #2d366b 80%);
    box-shadow: 0 0 12px 0 #666;
    transform: translateY(2px); }
